
hr {
  margin: 15px 0
}
input {
  width: 13%;
  margin: 6px 5px;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid black;
  text-align: center;
}
p {
  margin: 4px 0
}
input:focus {
  border: none;
  border-radius: 5px;
}
.helpinfo {
  display: table;
  margin: 10px auto;
  border: 1px solid black;
  padding: 10px;
  border-radius: 5px;
}
.row {
  display: table-row;
}
.cell {
  display: table-cell;
  text-align: left;
  padding-bottom: 10px;
}
.cell:first-child {
  padding: 0 20px;
}
button {
  padding: 10px 14px;
  margin: 10px 0;
  border-radius: 5px;
  border: 1px solid black;
}
.reverse {
  padding: 0;
  margin: 0;
  font-size: 140%;
  background: none;
  border: none;
}
.container {
  width: 320px;
  margin: 0 auto;
}
.box {
  border: 1px solid black;
  border-radius: 4px;
  padding: 5px 10px;
  margin: 5px 0
}
.sqf {
  font-size: 70%;
  vertical-align: text-top;
}
.decimal-input {
  width: 10%;
}
.results {
  text-align: left;
  font-size: 120%;
}
.cost .offset {
  width: 80px;
}




